import React from 'react'
import DetailsCard from "../../../components/personalDetails/personslDetailsCard"
import{DriverContext} from "../DriversContainer"
import {translate} from "../../../utils/translate"
import profile from "../../../assets/FACEBOOK_LINE-01-512.png"
import classes from "../../Riders/Tabs/tabs.module.css"
import Avatar from '@material-ui/core/Avatar';
import { Paper, Hidden } from '@material-ui/core'
import SummaryBookingCard from "../../../components/summaryBookingCard/summaryBookingCard";
import { theme } from '../../../config/theme'
import config from "../../../config/backendURLS";
import 'car-makes-icons/dist/style.css';
import ActionsMenu from "../../../components/actionsMenu/actionsMenu"
const driverPersonalDetails = (prop) => {
    return (
        <DriverContext.Consumer>
            {props=>(
                <>
                <DetailsCard>
                    <div className={classes.actionsMenu}>
                    <ActionsMenu
                        object={"DRIVER"}
                        status={props.state.driver.personal.verification_status}
                        onActionClicked={prop.onActionClicked}/>
                    </div>
                    <div className={classes.profilePhoto}>
                        <Avatar src={props.state.driver && props.state.driver.personal.profile_image?`${config.hostDomain}${props.state.driver.personal.profile_image}`:profile} alt="profile pic"  style={{width:75,height:75}}/>
                    </div>
                    <div className={classes.info}>
                        <div className={classes.part1}>
                        <div style={{display:"flex"}}>
                            <h4 style={{margin:0,fontWeight:"600"}}>{props.state.driver && props.state.driver.personal.full_name}</h4>
                        </div>
                        <div style={{display:"flex",alignItems:"center"}}>
                                <h5 style={{margin:"0 3px"}}>{translate("THE_NUMBER")}:</h5>
                            <h5 style={{direction:"ltr",margin:0}}>{props.state.driver && props.state.driver.personal.phone_number}</h5>
                        </div>
                        </div>
                        
                        <div className={classes.part2}>
                        <div style={{display:"flex",alignItems:"center"}}>
                                <h5 style={{margin:"0 3px"}}>{translate("RATING")}:</h5>
                            <h5 style={{margin:0}}>{props.state.driver && props.state.driver.personal.rating}</h5>
                            </div>
                            <div style={{display:"flex",alignItems:"center"}}>
                                <h5 style={{margin:"0 3px"}}>{translate("GENDER")}:</h5>
                            <h5 style={{margin:0}}>{translate(props.state.driver && props.state.driver.personal.gender)}</h5>
                            </div>
                            <div style={{display:"flex",alignItems:"center"}}>
                                <h5 style={{margin:"0 3px"}}>{translate("STATUS")}:</h5>
                            <h5 style={{margin:0}}>{translate(props.state.driver && props.state.driver.personal.verification_status)}</h5>
                            </div>
                        </div>
                      
                    </div>
                </DetailsCard>
                <div style={{margin:"15px 0"}}>
                <div className={classes.carDetails}>
                    <Paper style={{background:theme().palette.primary.dark,padding:5,color:"#fff"}}>
                    <h4 style={{padding:0,margin:0}}>المركبات:</h4>
                    </Paper>
                    {props.state.driver.vehicles.map(vehicle=>{
                        return <div key={vehicle.id} className={classes.carInfo}>
                            <div className={classes.carInfoItem}>
                            <p style={{margin:"0 3px"}}>{translate("TYPE")}:</p>
                            <Hidden xsDown>
                            <p style={{margin:0,paddingTop:4}}>{vehicle.model}</p>
                            <p style={{margin:"0 4px",paddingTop:4}}>{vehicle.make}</p>
                            </Hidden>
                            <Hidden smUp>
                            <p style={{margin:"0 2px",paddingTop:4,fontSize:17}}><i className={`car-${vehicle.make.toLowerCase()}`}></i></p>
                            </Hidden>
                            
                            </div>
                            <div className={classes.carInfoItem}>
                                <p style={{margin:"0 3px"}}>الرقم:</p>
                                <p style={{margin:0}}>{vehicle.number}</p>
                            </div>
                            <div className={classes.carInfoItem}>
                            <Avatar src={`${config.hostDomain}${vehicle.image}`} alt="car pic"  classes={{root:classes.carAvatar}}/>
                            </div>
                        </div>
                   
                    })}
                    </div>
                </div>
                <div className={classes.trips}>
                <Paper style={{background:theme().palette.primary.dark,padding:5,color:"#fff"}}>
                    <h4 style={{padding:0,margin:0}}>{translate("RIDES")}:</h4>
                </Paper>
                {props.state.driver && !props.state.isLoadingDriver && props.state.driver.rides.active_rides.length!==0?<div>
                    {props.state.driver.rides.active_rides.map(ride=>{
                    
                        return <SummaryBookingCard id="driver" key={ride.id} trip={ride}/>
                    })}
                    </div>
                    :null}
                    {props.state.driver && !props.state.isLoadingDriver && props.state.driver.rides.last_rides.length!==0?<div>
                    {props.state.driver.rides.last_rides.map(ride=>{
                    return <SummaryBookingCard id="driver" key={ride.id} trip={ride}/>
                    })}
                    </div>
                    :null}
            </div>
            {props.state.driver && !props.state.isLoadingDriver && props.state.driver.rides.active_rides.length===0 && props.state.driver.rides.last_rides.length===0?
               <div style={{
                display:"flex"
                ,alignItems:"center"
                ,justifyContent:"center"}}>
                <h3>{translate("NO_TRIPS")}</h3>
              </div>:null}
                </>
            )}
        </DriverContext.Consumer>
        
    )
}



export default driverPersonalDetails
